  const cmap_options = ['', 'Reds','YlGnBu']

  const [averagedColorData, setAveragedColorData] = useState(average3DArray(eventfileData, +colorCol));

  const [loading, setLoading] = useState(false);
  const [dataset, setDataset] = useState("all");
  const [dr, setDR] = useState("tsne");

